import { Component, OnInit, OnDestroy, Output, EventEmitter, ElementRef, ViewChild, Input } from '@angular/core';
import { CalcService, NumberFormatting } from '@btsdigital/ngx-isomer-core';
import * as noUiSlider from 'nouislider';


@Component({
  selector: 'isma-toggle-slider',
  templateUrl: './toggle-slider.component.html',
  styleUrls: ['./toggle-slider.component.styl']
})
export class ToggleSliderComponent implements OnInit, OnDestroy {

  @Input() value = 0;
  min = -1;
  max = 1;
  step = 1;
  slider: noUiSlider.Instance;

  @Input() minLabel: string = 'False';
  @Input() maxLabel: string = 'True';

  @Input() isDisabled: boolean = false;
  @Input() isCorrect: boolean;

  @Output() onChange: EventEmitter<any> = new EventEmitter<any>();
  @ViewChild('sliderEl') sliderEl: ElementRef;

  constructor(private calcService: CalcService) {}

  ngOnInit() {
    const opt: noUiSlider.Options = {
      start: 0,
      range: {
        'min': Number(this.min),
        'max': Number(this.max)
      },
      step: this.step,
      behaviour: 'tap'
    };
    this.slider = this.sliderEl.nativeElement;
    noUiSlider.create(this.slider, opt);
    this.bindEvents();
  }

  ngOnDestroy() {
    // this.slider.noUiSlider.off('change');
  }

  getValue(): number {
    return this.value;
  }

  setValue(val: number) {
    val = Number(val); // make sure val is a number value
    this.slider.noUiSlider.set(val);
    this.slider.noUiSlider.updateOptions({start: 0, step: 2, range: { min: Number(this.min), max: Number(this.max)}});
    this.value = val;
    this.onChange.emit(val);
  }

  bindEvents() {
    if (this.slider.noUiSlider) {
      this.slider.noUiSlider.on('change', () => {
        const curVal = this.slider.noUiSlider.get();
        this.slider.noUiSlider.updateOptions({start: 0, step: 2, range: { min: Number(this.min), max: Number(this.max)}});
        this.value = Number(curVal);
        this.onChange.emit(this.value);
      });
    }
  }
}



