import { ChangeDetectorRef, Component, HostListener, Input, OnDestroy, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, CommunicatorService, Constants, HttpWrapperService, StorageService, TextService } from '@btsdigital/ngx-isomer-core';
import { ModalDirective } from 'ngx-bootstrap';
import { Subscription } from 'rxjs/Subscription';
import { environment } from '../../../environments/environment';
import { AppConstants } from '../../shared/app-constants';
import { TextHelperService } from '../../shared/text-helper.service';
import { FileSaver } from '../../shared/utils/filesaver';
import * as _ from 'lodash';

declare var nw: any;
declare var chrome: any;

const TIMER_START_VAL = {
  'ROUND_1': '2700', // 45 mins
  'ROUND_2': '2700', // 45 mins
  'ROUND_3': '1800' // 30 mins
};

const KEY_NUM_5 = 53; // used to bring up timeSetter modal
const KEY_NUM_9 = 57; // used to forcefully end the round
const KEY_NUM_0 = 48; // used to reset sim
const KEY_NUM_6 = 54; // used to export Inputs
const KEY_NUM_7 = 55; // used to import data

@Component({
  selector: 'isma-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.styl']
})
export class HeaderComponent implements OnInit, OnDestroy, AfterViewInit {
  roundUpdated = false;
  REFS: any = {};
  currentYear: string;
  timerVal = '0';
  roundStartTime: string;
  showTimesUpHold = false;
  connectToPulse = environment.connectToPulse;
  private subscriptions: Subscription[] = [];
  progressValue: number = 0;
  isFeedbackVisible: boolean = false;
  infoIndex: number = 1;

  @Input() topMetrics: any;
  @Input() headerData: any;

  // @ViewChild('instructions') public instructionsRef;
  // @ViewChild('instructionsModal') public instructionsPage;
  @ViewChild('logoutModal') public logoutModalRef: ModalDirective;
  @ViewChild('myTimer') public myTimerRef;
  @ViewChild('timeSetterModal') public timeSetterModal: ModalDirective;
  @ViewChild('importbalances') importBalancesEl;
  @ViewChild('importsuccess') importsuccessModalRef;
  @ViewChild('showInfo') showInfoModalRef: ModalDirective;

  @HostListener('document:keyup', ['$event'])
  triggerShortcuts($event: KeyboardEvent) {
    if ($event.shiftKey && $event.keyCode === KEY_NUM_0) {
      this.resetSim();
    }
    if ($event.shiftKey && $event.keyCode === KEY_NUM_9) {
      this.endRound();
    }
    if ($event.shiftKey && $event.keyCode === KEY_NUM_5) {
      this.timeSetterModal.show();
    }
    if ($event.keyCode == KEY_NUM_6 && $event.shiftKey) {
      this.exportDecisions();
    }
    if ($event.keyCode == KEY_NUM_7 && $event.shiftKey) {
      this.importBalances();
    }
  }

  constructor(private calcService: CalcService, private textService: TextService,
    private storageService: StorageService, private cdRef: ChangeDetectorRef,
    private communicator: CommunicatorService, private httpWrapper: HttpWrapperService,
    private router: Router, private textHelper: TextHelperService, private fileSaver: FileSaver) { }

  ngOnInit() {
    // this.ensureIntroComplete().then((completed)=>{
    //   if(!completed){
    //     this.showInfoModalRef.show();
    //   }
    // })
    this.updateProgress();
    this.checkIfFeedbackVisible();

    this.currentYear = this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND);
    this.roundStartTime = TIMER_START_VAL['ROUND_' + this.currentYear];
    

    this.storageService.getValue(AppConstants.START_TIMER)
      .then((result) => {
        if (result === 'true') {
          this.myTimerRef.startTimer();
        }
      });

    const startTimerSub = this.communicator.getEmitter(AppConstants.START_TIMER).subscribe((result) => {
      if (result === 'true') {
        this.myTimerRef.startTimer();
      }
    });

    const startNextRoundSub = this.communicator.getEmitter(AppConstants.START_NEXT_ROUND)
      .subscribe(() => {
        this.startNextRound();
      });

    const calcCompleteSub = this.communicator.getEmitter(Constants.MODEL_CALC_COMPLETE)
      .subscribe(() => {
        this.calcCompleteSubscription();
      });

    this.subscriptions = [startTimerSub, startNextRoundSub, calcCompleteSub];
  }

  ngOnDestroy() {
    _.each(this.subscriptions, (n) => {
      if (n) {
        n.unsubscribe();
      }
    });
  }

  ngAfterViewInit(){
    if(!this.ensureIntroComplete()){
      this.showInfoModalRef.show();
    }
  }

  resetTimer() {
    this.myTimerRef.stopTimer();
    const startTime = TIMER_START_VAL['ROUND_' + this.currentYear];

    Promise.all([this.storageService.setValue(AppConstants.ROUND_TIMER, startTime),
    this.storageService.setValue(AppConstants.START_TIMER, 'true')])
      .then(() => {
        this.myTimerRef.startTimer();
      })
      .catch(() => {
        this.myTimerRef.startTimer(startTime);
      })
      .then(() => {
        this.communicator.trigger(AppConstants.START_TIMER, true);
      });
  }

  calcCompleteSubscription() {
    this.roundUpdated = false;
    const year = this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND);
    if (this.currentYear !== year) {
      this.roundUpdated = true;
      this.currentYear = year;
      this.showTimesUpHold = false;
      this.resetTimer();
      this.communicator.trigger(AppConstants.CLOSE_EOR_FEEDBACK, true);
      // close end of round feedback / navigate to dashboard
      // this.router.navigateByUrl('/dashboard');
    }
    this.updateProgress();
    this.checkIfFeedbackVisible();
  }

  // onClickInstructions() {
  //   this.instructionsPage.showChildModal();
  // }
  updateProgress() {
    let value = this.calcService.getValueForYear('tlOutput_ProgressBar', 'tlInputRound');
    value = value.replace("%",'');
    this.progressValue = Number(value);
  }

  checkIfFeedbackVisible(){
    const value = Number(this.calcService.getValueForYear('tlOutput_Trigger_CanSubmit', 'tlInputRound'));
    this.isFeedbackVisible = value === 1 ? true : false;
  }

  showLogoutAlert() {
    this.logoutModalRef.show();
  }

  hideLogoutAlert(confirmed: boolean = false) {
    this.logoutModalRef.hide();
  }

  updateTimer() {
    this.myTimerRef.stopTimer();
    Promise.all([this.storageService.setValue(AppConstants.ROUND_TIMER, this.timerVal),
    this.storageService
      .setValue(AppConstants.START_TIMER, 'true')])
      .then(() => {
        this.myTimerRef.startTimer();
      }).catch(() => {
        this.myTimerRef.startTimer(this.timerVal);
      })
      .then(() => {
        this.communicator.trigger(AppConstants.START_TIMER);
      });
  }

  timerUp($event?: any) {
    if (this.showTimesUpHold !== true) {
      this.showTimesUpHold = true;
    }
    // close investments
    // this.investmentsPage.hideChildModal();
    // this.rdinvestmentsPage.hideChildModal();
    this.router.navigateByUrl('/dashboard');
    this.storageService.setValue(AppConstants.ROUND_TIMER, '0');
    setTimeout(() => {
      this.router.navigateByUrl('/roundfeedback/(year:' + this.currentYear + ')');
    }, 1000);
    this.cdRef.markForCheck();
    // this.dataStore.triggerChange(ROUND_TIMER);
    this.communicator.trigger(AppConstants.TIMER_UP_EVENT);
  }


  endRound() {
    const confirmResponse = confirm('Do you wish to end this round? You cannot undo this.');
    if(confirmResponse){
      this.communicator.trigger(AppConstants.PIN_CLICKED, true);
      // set timer to 0
      this.timerVal = '1';
      this.updateTimer();      
    }
  }

  startNextRound() {
    const newYear = Number(this.currentYear) + 1;
    this.storageService.setValue(Constants.ROUND_VALUE, newYear + '');
    this.calcService.setValue(AppConstants.REFS.CURRENT_ROUND, newYear, false, newYear + '', 'isma-header').then(() => {
    this.calcService.setValueForYear('tlInputRoundStarted', 1, AppConstants.REFS.CURRENT_ROUND)
      this.showTimesUpHold = false;
      this.resetTimer();
      this.cdRef.markForCheck();
    });
  }

  resetSim() {
    // localStorage.clear();
    const confirmResponse = confirm('Do you wish to reset sim progress? You cannot undo this.');
    if (confirmResponse) {
      let ManifestZone = window.localStorage.getItem('ManifestHost');
      ManifestZone = (ManifestZone !== null || ManifestZone !== 'null') ? ManifestZone : null;
      this.storageService.clearAll()
        .then(() => {
          // if manifest zone is set, lets put it back in localstorage.
          if (ManifestZone) {
            window.localStorage.setItem('ManifestZone', ManifestZone);
          }
          if (typeof chrome !== 'undefined' && chrome.runtime && chrome.runtime.reload) {
            chrome.runtime.reload();
          } else {
            window.location.reload();
          }
        });
    }
  }

  winclose() {
    if (typeof nw !== 'undefined' && nw.App) {
      const confirmResponse = confirm('Do you wish to exit?');
      if (confirmResponse) {
        nw.App.closeAllWindows();
      }
    }
  }

  // local export data method to work with ism-calc-service
  exportDecisions() {
    // this.calcService.exportData(true, this.exportRangeNames);
    const modelState = this.calcService.exportData('^tlInput.+$');
    const teamName = this.calcService.getValue('tlInputTeamName');
    this.exportDecisionsOfISM(modelState, teamName);
  }

  // For values from ISM
  exportDecisionsOfISM(modelState, teamName) {
    const self = this;
    const fileNamePrefix = 'Leaderday2019_';
    const fileName = fileNamePrefix + 'Team_' + teamName + '.csv';
    const delim = ',';
    self.fileSaver.fileSaveAs(fileName, modelState, delim);
  }

  importBalances() {
    this.importBalancesEl.showFileChooser();
  }

  onFileLoaded(jsonOb) {
    const self = this;
    console.log('Loaded file', jsonOb);

    return new Promise((resolve, reject) => {
        try {
          this.calcService.appendDataToModel(jsonOb).then(() => {
          self.showSuccessAlert();
          resolve();
          }, reject);
        } catch (err) {
          reject(err);
        }
    });
  }

  showSuccessAlert() {
    this.importsuccessModalRef.show();
  }

  hideSuccessAlert() {
    this.importsuccessModalRef.hide();
  }

  hideInfoModal(){
    this.calcService.setValueForYear('tlInputRoundStarted', 1, 'tlInputRound').then(()=>{
      this.showInfoModalRef.hide();
    });
    // this.storageService.setValue(AppConstants.INTRO_COMPLETED, 'true');
  }

  private ensureIntroComplete(): boolean {
    let value = this.calcService.getValueForYear('tlInputRoundStarted', 'tlInputRound');
    if(value == "0"){
      return false;
    }else{
      return true;
    }
  }

}



