import {
  Component, OnInit, OnDestroy, ElementRef, ViewChild, AfterViewInit,
  ChangeDetectorRef, ChangeDetectionStrategy
} from '@angular/core';
import { Router } from '@angular/router';
import { CalcService, StorageService, ChartComponent, CommunicatorService, TextService } from '@btsdigital/ngx-isomer-core';
import { ModalDirective } from 'ngx-bootstrap';
import { TextHelperService } from '../../shared/text-helper.service';
import { AppConstants } from '../../shared/app-constants';
import { Subscription } from 'rxjs/Subscription';
import * as _ from 'lodash';

@Component({
  selector: 'isma-end-of-round-feedback',
  templateUrl: './end-of-round-feedback.component.html',
  styleUrls: ['./end-of-round-feedback.component.styl']
})
export class EndOfRoundFeedbackComponent implements OnInit, AfterViewInit {

  popup_cnt = 0;

  periods: any[];
  currentYear: number;
  totRounds: number = 1;
  roundTimerValue: string;
  allowClosingFeedback = false;
  buMetricPrefix = AppConstants.PREFIX.BU_METRIC;
  buMetricSuffix = AppConstants.SUFFIX.BU_SUMMARY_METRIC;
  roundSuffix = AppConstants.SUFFIX.ROUND;

  scenes: any;
  feedbackArray: any[] = [];
  levels: number[] = [1,2,3];
  chartData: number[] = [1, 2, 3, 4];
  quizPins: any[] = [];
  content: any[];
  showFeedback = false;
  toggled: boolean = false;
  contentOpt: string = '';
  voteDataOpt: any = [];
  quizSelected: any[] = [];
  passwordIncorrect: boolean;
  // clientRangeRef: string[];
  topMenuSel: number = 1;

  selectedEvent: any;
  scenario: string;
  feedbackText: string;
  choicesTitleText: string;
  feedbackTitle: string;
  localFeedbackChosen: string;
  feedbackName: string;
  backClicked: boolean;
  answer: string;
  optionSelected: boolean;
  enteredPassword: string;
  passwordToChangeRound2: string;
  passwordToChangeRound3: string;
  // metrics for EOR Feedback
  // Enterprise Metrics
  entRevenue = 'tlOutputCorp_Metric1';

  ocBtnDefault: number = 1;

  @ViewChild('directReportsChart') directReportsChart: ChartComponent;
  @ViewChild('orgEngChart') orgEngagementChart: ChartComponent;
  @ViewChild('passwordModal') public passwordModalRef: ModalDirective;

  private subscription: Subscription;

  constructor(private calcService: CalcService, private textHelperService: TextHelperService,
    private router: Router, private elRef: ElementRef, private storageService: StorageService,
    private cdRef: ChangeDetectorRef, private communicator: CommunicatorService, private textService: TextService) { }

  ngOnInit() {
    // console.log(this.buMetricPrefix + '1' + this.buMetricSuffix + '1' + this.roundSuffix + '1', "<<<<");
    this.periods = [];
    this.totRounds = Number(this.calcService.getValue('StratSim_Rounds'));
    this.currentYear = Number(this.calcService.getValue('tlInputTeamYear'));
    this.passwordToChangeRound2 = this.textService.getText('Password2');
    this.passwordToChangeRound3 = this.textService.getText('Password3');

    const rawData: any = this.textHelperService.getRawScenes();
    this.loadContent(rawData);

    // this.generateEntRefsForRound(1);
    // this.generateEntRefsForRound(2);

    // this.getPinDetailsForYear(this.currentYear);
    this.storageService.getValue(AppConstants.ROUND_TIMER)
      .then((value: string) => {
        this.roundTimerValue = value;
        this.showFeedback = true;
      })
      .then(() => {
        for (let round = 1; round <= this.currentYear; round++) {
          // if (round === this.currentYear && this.roundTimerValue !== '0') {
          //   continue;
          // }

          this.getPinDetailsForYear(round);
          this.periods.push(round);
        }
      });
    this.subscription = this.communicator.getEmitter(AppConstants.CLOSE_EOR_FEEDBACK).subscribe((data) => {
      this.onClose();
    });
    this.cdRef.markForCheck();
  }

  ngAfterViewInit() {
    setTimeout(() => {
      window.dispatchEvent(new Event('resize'));
      this.cdRef.markForCheck();
    }, 500);
  }

  loadContent(rawData) {
    this.content = rawData.content;
    // console.log(JSON.stringify(this.content));
    this.content.forEach((scene) => {
      // console.log(scene.PageType);
      switch (scene.PageType) {
        case 'Quiz':
          this.quizPins.push(scene);
          break;
      }
    });
  }

  getPinDetailsForYear(year: number) {
    let completedActivity: any[];

    completedActivity = this.quizPins.filter((pin) => {
      return pin.ID.slice(pin.ID.lastIndexOf('_')).slice(1) === 'R' + year &&
        this.calcService.getValue(pin.pinDecisionVariable) !== '0';
    });
    // completedActivity.forEach((pin) => {
    //   const value = this.calcService.getValue(pin.pinDecisionVariable);
    //   pin['answer'] = value === '0' ? '0' : value === '1' ? 'Y' : 'N';
    // });

    
    this.quizSelected[year - 1] = [];

    this.levels.forEach((level) => {
      const quizForClient = completedActivity.filter((item) => {
        return Number(item.level) === level;
      });

      this.quizSelected[year - 1].push(quizForClient);
    });
  }

  startNextRound() {
    this.onClose(true);
  }

  onClose(startNextRound: boolean = false) {
    setTimeout(() => {
      this.allowClosingFeedback = true;
      // let url = this.utils.stripOutletFromUrl(this.router.url, AppConstants.YEAR_OUTLET);
      // this.router.navigateByUrl(this.utils.stripChildRouteFromUrl(url, AppConstants.EOR_FEEDBACK));
      this.router.navigateByUrl('/dashboard').then(() => {
        this.allowClosingFeedback = true;
        // if (startNextRound === true) {
        //   const data = { 'shiftKey': true, 'ctrlKey': true, code: 'Digit2', keyCode: 50, key: '@', keyIdentifier: '@' },
        //     customEvent = new KeyboardEvent('keyup', data);
        //   document.dispatchEvent(customEvent);
        //   this.cdRef.markForCheck();
        // }
        if (startNextRound) {
          const currentYear = this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND);
          // this.calcService.setValue('tlInputActions_R' + (this.currentYear + 1), '1', false, currentYear);
          this.communicator.trigger(AppConstants.START_NEXT_ROUND, true);
        }
      });
    }, 100);
  }

  onEventClick(Event) {
    return Event.answer === '0' ? Event.answer : Event.answer.charCodeAt(0) - 64;
  }

  onClick(val) {
    this.selectedEvent = val;
    const answer = val.PageType === 'Event' ? this.onEventClick(val) : this.onOpportunityClick(val);
    this.scenario = val['narrative'];
    this.feedbackText = (val['alt' + answer + '_narrative'] !== void 0 ? val['alt' + answer + '_narrative'] : '');
    this.choicesTitleText = String.fromCharCode(64 + answer) + ' : ' + val['alt' + answer];
    this.feedbackTitle = val['name'];
    this.localFeedbackChosen = String.fromCharCode(64 + answer);
    this.feedbackName = val['alt' + answer + '_feedback'];
    this.backClicked = false;
    this.answer = answer;
    this.optionSelected = true;
    setTimeout(() => {
      this.cdRef.markForCheck();
    }, 500);
    // this.showChildModal();
  }

  updateFeedbackText(id) {
    this.feedbackText = (this.selectedEvent) ? this.selectedEvent['alt' + id + '_narrative'] : '';
    // console.log('answer'+this.answer);
    if (this.answer === id) {
      this.optionSelected = true;
    } else {
      this.optionSelected = false;
    }
    this.choicesTitleText = (this.selectedEvent) ? this.getChar(id) + ': ' + this.selectedEvent['alt' + (id)] : '';
    // console.log('update: '+this.calcService.getValue('tlInput' + id));
  }

  hideFeedback(event: any) {
    this.backClicked = true;
    this.selectedEvent = null;
    this.answer = null;
    this.ngAfterViewInit();
  }

  onOpportunityClick(Opportunity) {
    return Opportunity.answer === '0' ? Opportunity.answer :
      Number(this.calcService.getValue(Opportunity.pinDecisionVariable)) === -1 ? 2 : 1;
  }

  backToEOR(){
    const self = this;
    const fcv = document.getElementsByClassName('popup')[1];
    fcv.classList.add('flip-card__doFlip');

    setTimeout(function () {
      self.popup_cnt = 0;
    }, 800);
  }

  showPrimarTextModal() {
    // this.passwordModalRef.show();
    const self = this;
    const fcv = document.getElementsByClassName('popup')[0];
    fcv.classList.add('flip-card__doFlip');

    setTimeout(function () {
      self.popup_cnt = 1;
    }, 800);
  }

  showPasswordModal() {
    // this.passwordModalRef.show();
    const self = this;
    const fcv = document.getElementsByClassName('popup')[1];
    fcv.classList.add('flip-card__doFlip');

    setTimeout(function () {
      self.popup_cnt = 2;
    }, 800);
  }

  hidePasswordModal() {
    this.passwordModalRef.hide();
  }

  onSubmitPasswordModal() {
    if ((this.enteredPassword === this.passwordToChangeRound2)) {
      if (this.currentYear == 1) {
        this.startNextRound();
      }else {
        this.passwordIncorrect = true;
        this.cdRef.markForCheck();
      }
    }else if((this.enteredPassword === this.passwordToChangeRound3)){
      if (this.currentYear == 2) {
        this.startNextRound();
      }else {
        this.passwordIncorrect = true;
        this.cdRef.markForCheck();
      }
    } else {
      this.passwordIncorrect = true;
      this.cdRef.markForCheck();
    }
  }

  scrollTo(id, selNo) {
    this.topMenuSel = selNo;
    const elem = document.getElementById(id);

    if (elem) {
      elem.scrollIntoView(true);
    }
  }

  scrollToElem(elem) {
    let scrollCount = 0,
      scrollMargin,
      lastStep;
    const scrollHeight = elem.offsetTop,
      scrollEl = this.elRef.nativeElement.querySelector('.modal-body'),
      scrollStep = Math.PI / (1500 / 15),
      cosParameter = Math.abs(scrollEl.scrollTop - scrollHeight) / 2,
      scrollDown = (scrollEl.scrollTop < scrollHeight) ? true : false,
      scrollInterval = setInterval(() => {
        if (scrollEl.scrollTop < scrollHeight && scrollDown) {
          scrollCount = scrollCount + 1;
          scrollMargin = cosParameter - cosParameter * Math.cos(scrollCount * scrollStep);
          scrollEl.scrollTop = (scrollEl.scrollTop + scrollMargin);
        } else if (scrollEl.scrollTop > scrollHeight && !scrollDown) {
          scrollCount = scrollCount + 1;
          scrollMargin = cosParameter - cosParameter * Math.cos(scrollCount * scrollStep);
          scrollEl.scrollTop = (scrollEl.scrollTop - scrollMargin);
        } else {
          clearInterval(scrollInterval);
        }
        if (lastStep === scrollEl.scrollTop) {
          clearInterval(scrollInterval);
        }
        lastStep = scrollEl.scrollTop;
      }, 15);
  }

  getChar(index) {
    return String.fromCharCode(64 + index);
  }

  canDeactivate(): boolean {
    return (this.allowClosingFeedback || this.roundTimerValue !== '0');
  }

  gotoOther(contentOpt, dataOpt) {
    this.contentOpt = contentOpt;
    this.voteDataOpt = dataOpt;
    this.toggled = true;
  }
}



