import { Component, Input, OnInit, OnDestroy, OnChanges, ChangeDetectionStrategy, ChangeDetectorRef, Output, EventEmitter, HostListener } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { Constants, CalcService, CommunicatorService } from '@btsdigital/ngx-isomer-core';

const KEY_ENTER = 13;

@Component({
  selector: 'isma-calc-textarea',
  templateUrl: './calc-textarea.component.html',
  styleUrls: ['./calc-textarea.component.styl']
})
export class CalcTextareaComponent implements OnInit {

    value: string;
    text: string;
    private subscription: Subscription;
    private isDirty: boolean = false;

    @Input() ref: string;
    @Input() yearRef: string;
    @Input() readOnly: Boolean;
    @Input() placeholderText: string = 'Enter response here...';
    @Input() rows: number = 1;
    @Input() maxlength: number;
    @Input() allowEnter: boolean = true;

    @Input() hasValidations: boolean = false;
    @Input() validationsText: string = "";
    validations = []
    isInvalid: boolean = false;

    @Output() enterPressed = new EventEmitter();
    // @HostListener('document:keyup', ['$event'])
    //     onEnterKeyPress($event: KeyboardEvent) {
    //     if ($event.keyCode === KEY_ENTER) {
    //         this.emitEnterEvent();
    //         return false;
    //     }
    // }

    constructor(private calcService: CalcService, private communicator: CommunicatorService,
        private cdRef: ChangeDetectorRef) { }

    ngOnInit() {
        this.subscription = this.communicator.getEmitter(Constants.MODEL_CALC_COMPLETE)
            .subscribe(() => {
                this.update();
                this.cdRef.markForCheck();
            });

        this.update();
        //track data on load
        // this.isomerXapiService.trackDataOnLoad(this.calcService.getWorksheetName(this.ref), this.calcService.getRefNameByYearRef(this.ref, this.yearRef), String(this.value), this.yearRef ? this.calcService.getValue(this.yearRef) : this.calcService.getRoundValue(), 'calc-textarea');
        if(this.hasValidations){
            this.validations = this.validationsText.split(',');
            this.isInvalid = !this.validations.includes(this.value);
        }
    }

    ngOnChanges() {
        this.update();
    }

    ngOnDestroy() {
        this.subscription.unsubscribe();
    }

    update() {
        this.value = this.calcService.getValueForYear(this.ref, this.yearRef);
        this.text = this.value == "0" ? '' : this.value;
    }

    saveDataToModel($event?: any) {
        if(this.readOnly){
            return;
        }

        if(this.text == ''){
            this.value = '0';
        }else{
            this.value = this.text;
        }
        
        if(this.isDirty){
            if (this.value.match(/^\d/)) {
                this.value = '`' + this.value;
            }
            this.calcService.setValueForYear(this.ref, this.value, this.yearRef, 'calc-textarea');
            this.isDirty = false;
        }

        if(this.hasValidations){
            this.isInvalid = !this.validations.includes(this.value);
        }
    }

    onModelChange(val: any) {
        if (val === '') {
            return;
        }
        this.text = val;
        this.isDirty = true;
    }

    emitEnterEvent(){
        this.enterPressed.emit(null);
    }

    checkIfEnterPressed(e){
        if(!this.allowEnter && e.keyCode == KEY_ENTER){
            e.preventDefault();
            e.stopPropagation();
            this.emitEnterEvent()
        }
    }
}


