import { Component, OnInit, Input, OnDestroy, ViewEncapsulation, ChangeDetectorRef } from '@angular/core';
import { CalcService, TextService, CommunicatorService, Constants } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs/Rx';
import { AppConstants } from '../../shared/app-constants';

const MAX_BU_METRICS = 4;
const FORMATS_MAP = {
  1: { // Eng and Dev
    formats: [
      '0:0.01', // percent - Direct reports eng %
      '0:0.01', // percent - Org eng %
      '0.0%', // percent - Prod rev growth %
      '0.0%', // percent - Market Share %
      '0.0%', // $0.00a - Operating Margin
      '0.0', // number - Quality Index
      '0.0', // number - Innovation Index
      '0.0', // number - CoOperation Index
    ]
  },
  2: { // sales
    formats: [
      '0:0.01', // percent - Direct reports eng %
      '0:0.01', // percent - Org eng %
      '$0.0a', // $0.00a - Pipeline
      '$0.0a', // $0.00a - Partner bookings %
      '0.0%', // percent - Sales productivity
      '0.0', // number - Partner Health Index
      '0.0', // number - Execution Index
      '0.0', // number - CoOperation Index
    ]
  },
  3: { // Customer Exp
    formats: [
      '0:0.01', // percent - Direct reports eng %
      '0:0.01', // percent - Org eng %
      '0.0%', // percent - Recurring revenue %
      '0.0%', // percent - Renewal rate %
      '0.0%', // percent - Gross Margin
      '0.0', // number - NPS Index
      '0.0', // number - Innovation Index
      '0.0', // number - CoOperation Index
    ]
  },
};

@Component({
  selector: 'isma-dash-metrics',
  templateUrl: './dash-metrics.component.html',
  styleUrls: ['./dash-metrics.component.styl'],
  encapsulation: ViewEncapsulation.None
})
export class DashMetricsComponent implements OnInit, OnDestroy {

  @Input() businessUnitId: number;
  @Input() isEOF: boolean;
  @Input() dashMetricsData: any;
  currentRound: string;
  buMetricPrefix = AppConstants.PREFIX.BU_METRIC;
  buMetricSuffix = AppConstants.SUFFIX.BU_SUMMARY_METRIC;
  roundSuffix = AppConstants.SUFFIX.ROUND;
  metricLabels: string[] = [];
  metricRefs: string[] = [];
  metricFormats: string[] = [];
  metricValues: number[] = [];
  // submetrixballData: Array<Object>;
  // leaderboardData: Array<Object>;
  private subscription: Subscription;

  constructor(private calcService: CalcService, private textService: TextService,
    private communicator: CommunicatorService, private cdRef: ChangeDetectorRef) { }

  ngOnInit() {
    // this.currentRound = this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND);
    // generate metrics
    this.updateMetricRefs();

    this.subscription = this.communicator.getEmitter(Constants.MODEL_CALC_COMPLETE).subscribe(() => {
      const year = this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND);
      if (year !== this.currentRound) {
        this.currentRound = year;
        this.updateMetricRefs();
      } else {
        this.metricValues = [];
        for (let i = 0; i < MAX_BU_METRICS; i++) {
          const textKey = 'BU' + this.businessUnitId + '_SummaryMetric1',
            textString = this.textService.getText(textKey) || textKey,
            ref = AppConstants.PREFIX.BU_METRIC + this.businessUnitId + AppConstants.SUFFIX.BU_SUMMARY_METRIC + (i + 1) +
              AppConstants.SUFFIX.ROUND + this.currentRound;
          this.metricValues.push(Number(this.calcService.getValue(ref, true)));
        }
      }
      this.cdRef.markForCheck();
    });
    this.cdRef.markForCheck();
    // this.leaderboardData = [
    //   {metricTitle: 'Sales NPS', value: 'tlOutputBU1_SummaryMetric3_R1'},
    //   {metricTitle: 'Proposal Win Rate', value: 'tlOutputBU1_SummaryMetric4_R1'},
    //   {metricTitle: 'Global Page Views', value: 'tlOutputBU1_SummaryMetric5_R1'}
    // ];

    // this.submetrixballData = [
    //   {buttons: [{ name: 'REPORT', dropdown: false }, { name: 'SWOT', dropdown: false }, { name: 'SUBMETRIX', dropdown: true }]},
    //   {text: [{name: 'Responsive'}, {name: 'Client Focus'}, {name: 'Empl Experience'}]},
    //   {metrics: [{ref: 'tlOutputBU1_SummaryMetric6_R1'}, {ref: 'tlOutputBU1_SummaryMetric7_R1'}, {ref: 'tlOutputBU1_SummaryMetric8_R1'}]}
    // ];

  }

  updateMetricRefs() {
    this.currentRound = this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND);
    this.metricLabels = [];
    this.metricValues = [];
    this.metricFormats = [];
    this.metricRefs = [];
    for (let i = 0; i < MAX_BU_METRICS; i++) {
      const textKey = 'BU' + this.businessUnitId + '_SummaryMetric1',
        textString = this.textService.getText(textKey) || textKey,
        ref = AppConstants.PREFIX.BU_METRIC + this.businessUnitId + AppConstants.SUFFIX.BU_SUMMARY_METRIC + (i + 1) +
          AppConstants.SUFFIX.ROUND + this.currentRound;
      this.metricLabels.push(textString);
      this.metricRefs.push(ref);
      // this.metricFormats.push(FORMATS_MAP[this.businessUnitId]['formats'][i]);
      this.metricValues.push(Number(this.calcService.getValue(ref, true)));
    }
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
}



