import {
  Component, OnInit, Input, ChangeDetectorRef, ChangeDetectionStrategy,
  ViewChild, ElementRef, OnDestroy
} from '@angular/core';
import { CalcService, TextService, CommunicatorService, Constants, IsomerXapiService } from '@btsdigital/ngx-isomer-core';
import { ModalDirective } from 'ngx-bootstrap';
import { Observable, Subscription } from 'rxjs/Rx';
import { InitiativeType } from '../../shared/initiative-type.enum';
import { AppConstants } from '../../shared/app-constants';
import * as _ from 'lodash';

let INITIATIVE_WIDTH = 136;
@Component({
  selector: 'isma-investments',
  templateUrl: './investments.component.html',
  styleUrls: ['./investments.component.styl'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class InvestmentsComponent implements OnInit, OnDestroy {

  @Input() title: string;
  @Input() isVisible: boolean;
  @Input() content: any;
  @Input() investmentsArray: any[];
  @Input() categoryList: string[];
  @Input() initiativeType: number;
  @ViewChild('lgModal') public childModal: ModalDirective;
  @ViewChild('stepchildModal') public stepChildModal: ModalDirective;

  private subscription: Subscription;
  tlInputInvestment: string;
  isChecked = 0;
  investmentsChosen: any[] = [];
  description: string;
  header: string;
  maxChecks = 3;
  totalChecked = 0;
  currentYear = '0';
  currentlyHighlighted: any;
  currentSelectedInvestments: Array<any>;
  defaultDescription: string;
  routeAnimation: string;
  initiativesVisible: number;
  minMarginLeft: number;
  selectionRemainingRef: string;
  scrollLeftDisabled = false;
  scrollRightDisabled = false;
  filters: Array<string> = [];
  hasCategories = false;
  categories: Array<string> = [];
  isFiltered = false;
  el: HTMLElement;
  initsRemaining: string;
  showConfirmationModal: boolean;
  currentInitiativeIndex: number = 0;

  constructor(private elRef: ElementRef, private calcService: CalcService, private textService: TextService,
    private communicator: CommunicatorService, private cdRef: ChangeDetectorRef, private isomerXapiService: IsomerXapiService) { }

  ngOnInit() {
    if (screen.width <= 1024) {
      INITIATIVE_WIDTH = 114;
    }
    this.routeAnimation = 'flipSimple';
    this.defaultDescription = this.textService.getText('InvestmentInstructions');
    this.currentYear = this.calcService.getValue('tlInputTeamYear');
    if (this.investmentsArray[0]) {
      this.currentlyHighlighted = this.investmentsArray[0];
      this.investmentsArray[0].isSelected = !this.investmentsArray[0].isSelected;
      this.header = this.investmentsArray[0].name;
      this.description = this.investmentsArray[0].narrative;
    }
    this.currentSelectedInvestments = [];
    this.el = this.elRef.nativeElement;
    this.updateElemCount();
    this.loadCategories();
    this.initiativeType = Number(this.initiativeType);
    this.maxChecks = (this.initiativeType === InitiativeType.INITIATIVE) ? AppConstants.MAX_INITIATIVES_PER_ROUND :
      AppConstants.MAX_LEADER_ACTIONS_PER_ROUND;
    this.selectionRemainingRef = (this.initiativeType === InitiativeType.LEADER_ACTION) ? AppConstants.PREFIX.LEADER_ACTIONS_REMAIN :
      AppConstants.PREFIX.INITIATIVES_REMAIN;
    this.initsRemaining = this.calcService.getValueForYear(this.selectionRemainingRef, AppConstants.REFS.CURRENT_ROUND);
    this.cdRef.markForCheck();

    this.subscription = this.communicator.getEmitter(Constants.MODEL_CALC_COMPLETE)
      .subscribe(() => {
        this.initsRemaining = this.calcService.getValueForYear(this.selectionRemainingRef, AppConstants.REFS.CURRENT_ROUND);
        this.cdRef.markForCheck();
      });
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  loadCategories() {
    if (this.categoryList && this.categoryList.length) {
      this.hasCategories = true;
      this.categories = [];
      this.categoryList.forEach((categoryKey) => {
        this.categories.push(this.textService.getText(categoryKey));
      });
    }
  }

  updateElemCount() {
    // -1068px
    const nodes: any = this.el.getElementsByClassName('initiative'),
      elems: Array<HTMLElement> = Array.prototype.slice.call(nodes);
    this.initiativesVisible = 0;
    elems.forEach((elem, elemIndex) => {
      if (!!(elem.offsetWidth || elem.offsetHeight || elem.getClientRects().length)) {
        // this.investmentsArray[0][elemIndex].isSelected = true;
        this.initiativesVisible++;
      }
    });
    // -12 is the offset by which the last initiative is cutoff
    this.minMarginLeft = (((this.initiativesVisible - 6) * INITIATIVE_WIDTH)) * -1;
  }

  toggleFilter(ci: string) {
    const filterIndex = this.filters.indexOf(ci);
    if (filterIndex === -1) {
      this.filters.push(ci);
    } else {
      this.filters.splice(filterIndex, 1);
      // when removing a filter
      this.updateSelection();
    }
    // if selecting the first filter
    if (filterIndex === -1 && this.filters.length === 1) {
      this.updateSelection();
    }
    this.isFiltered = (this.filters.length !== 0) ? true : false;

    // update elem count after a cd cycle
    setTimeout(() => {
      this.updateElemCount();
    }, 100);
  }

  updateSelection() {
    // added timeout to let angular run one cd check and update dom and hide unwanted elems
    // from the filter and then update selection in cycle.
    // setTimeout(() => {
    const nodes: any = this.el.getElementsByClassName('initiative'),
      elems: Array<HTMLElement> = Array.prototype.slice.call(nodes),
      initiativesEl: any = this.el.getElementsByClassName('screen')[0];
    let elemFound: Boolean = false;
    initiativesEl.style.marginLeft = '0px';
    elems.forEach((elem, elemIndex) => {
      if (!!(elem.offsetWidth || elem.offsetHeight || elem.getClientRects().length) && !elemFound) {
        // this.investmentsArray[0][elemIndex].isSelected = true;
        elemFound = true;
        this.onSelect(null, elemIndex, null);
      }
    });
    this.cdRef.markForCheck();
    // }, 100);
  }

  getFilters() {
    return this.filters.map(i => 'cat-' + i).join(' ');
  }

  scroll(direction) {
    this.updateElemCount();
    const initiativesEl: any = this.el.getElementsByClassName('screen')[0],
      initiativesLength: number = initiativesEl.querySelectorAll('.initiative').length;
    let marginLeft = parseInt(window.getComputedStyle(initiativesEl)['margin-left'], 10);
    // scroll by 3 elements with
    if (direction !== 0) { // scroll right
      let scrollByElems = 0;
      const nextScroll = initiativesLength - (parseInt((-marginLeft / INITIATIVE_WIDTH) + '', 10) + 10);
      scrollByElems = (nextScroll >= 0) ? 6 : (6 + nextScroll);
      marginLeft = marginLeft - (scrollByElems * INITIATIVE_WIDTH);
      if (scrollByElems === 0) {
        marginLeft = this.minMarginLeft;
      }
      marginLeft = (marginLeft <= this.minMarginLeft) ? this.minMarginLeft : marginLeft;
    } else { // scroll left
      marginLeft = marginLeft + (6 * INITIATIVE_WIDTH);
      marginLeft = (marginLeft >= 0) ? 0 : marginLeft;
    }
    this.scrollLeftDisabled = (marginLeft <= 0) ? false : true;
    this.scrollRightDisabled = (marginLeft >= this.minMarginLeft) ? false : true;
    initiativesEl.style.marginLeft = marginLeft + 'px';
  }

  onSubmit() {
    // if inits remaining == 0 then do nothing.
    if (this.initsRemaining === '0') {
      return;
    }
    this.isVisible = false;
    this.hideChildModal();
    const refPrefix: string = (this.initiativeType === InitiativeType.INITIATIVE) ? AppConstants.PREFIX.INITIATIVE_CHOICE :
      AppConstants.PREFIX.LEADER_ACTION_CHOICE;
    let currentInit: any,
      localValue: any;
    // setTimeout(() => {
    // for (let i = 0; i < this.investmentsArray.length; i++) {
    //   for (let j = 0; j < this.investmentsArray[i].length; j++) {
    //     this.investmentsArray[i][j].isLocked = (this.investmentsArray[i][j].isChecked === 1);
    //   }
    // }
    this.investmentsArray.forEach((investment, index) => {
      investment.isLocked = (investment.isChecked === 1);
    });

    // reset initiative model ranges before setting new ones
    for (let i = 1; i <= this.maxChecks; i++) {
      currentInit = 'tlInput' + refPrefix + i + AppConstants.SUFFIX.ROUND + this.currentYear;
      localValue = Number(this.calcService.getValue(currentInit));
      if (localValue !== 0) {
        this.calcService.setValue(currentInit, 0, false, this.currentYear, 'isma-investments');
      }
    }
    this.totalChecked = 0;

    for (let i = 0; i < this.currentSelectedInvestments.length; i++) {
      const selectedInit = this.currentSelectedInvestments[i];
      // if current initiative is not selected in this round, move to next initiative in array
      if (Number(selectedInit.isChecked) !== Number(this.currentYear)) {
        continue;
      }
      // if initiative was checked this year
      this.totalChecked++;
      // set it in the next slot in the model for this year
      currentInit = 'tlInput' + refPrefix + this.totalChecked + AppConstants.SUFFIX.ROUND + this.currentYear;
      // currentInit = 'tlInput' + 'R' + this.currentYear + refPrefix + this.totalChecked;
      localValue = this.calcService.getValue(currentInit);
      // const selected = selectedInit.pinID.slice(0, 1) + Number(selectedInit.pinID.slice(1));
      const selected = ((this.initiativeType === InitiativeType.INITIATIVE) ? AppConstants.PREFIX.INITIATIVE_CHOICE :
        AppConstants.PREFIX.LEADER_ACTION_CHOICE) + Number(selectedInit.pinID.slice(1));
      if (localValue !== selected) {
        this.calcService.setValue(currentInit, selected, false, this.currentYear, 'isma-investments');
      }
    }
    this.cdRef.markForCheck();
    // }, 300);
  }

  // clicked on 'checkbox' to make/unmake initiative choice for the year
  // the .isChecked attribute contains the year in which the initiative was chosen (1, 2, ...)
  onCheck($event, index, ID) {
    if ($event) {
      $event.stopPropagation();
    }
    this.onSelect($event, index, ID);
    const refPrefix: string = (this.initiativeType === InitiativeType.INITIATIVE) ? AppConstants.PREFIX.INITIATIVE_CHOICE :
      AppConstants.PREFIX.LEADER_ACTION_CHOICE;
    // check/uncheck initiative
    if (this.investmentsArray[index].isEnabled) {
      this.investmentsArray[index].isChecked = (this.investmentsArray[index].isChecked > 0 ?
        0 : Number(this.currentYear));
      this.totalChecked = 0;
      this.currentSelectedInvestments.length = 0;
      // check if the investment / initiative is checked and set it in the model
      this.investmentsArray.forEach((investment: any, _index) => {
        // check if initiative/investment was checked this year
        if (investment.isChecked === Number(this.currentYear)) {
          this.totalChecked++;
          // set it in the next slot in model for this year
          const currentInit = 'tlInput' + refPrefix + this.totalChecked + AppConstants.SUFFIX.ROUND + this.currentYear;
          this.currentSelectedInvestments.push(investment);
        }
      });
      this.currentlyHighlighted = this.investmentsArray[index];
      this.setInitiativesEnabled();
      this.cdRef.markForCheck();
    }
  }

  private setInitiativesEnabled() {
    // check if the remaining initiatives are to be disabled after checking
    const refPrefix: string = (this.initiativeType === InitiativeType.INITIATIVE) ? AppConstants.PREFIX.INITIATIVE_CHOICE :
      AppConstants.PREFIX.LEADER_ACTION_CHOICE;
    this.investmentsArray.forEach((investment: any, _index) => {
      const currentRef = 'tlInput' + refPrefix + (_index + 1) + AppConstants.SUFFIX.ROUND + this.currentYear;
      this.isomerXapiService.trackDataOnLoad(this.calcService.getWorksheetName(currentRef), currentRef, 0, this.currentYear, 'isma-investemnts', false);
      investment.isEnabled = (!investment.isLocked && (investment.isChecked === Number(this.currentYear) ||
        (investment.isChecked === 0 && this.totalChecked < this.maxChecks)));
    });
  }

  // clicked on initiative name to read description
  onSelect($event, index, ID) {
    if ($event) {
      $event.stopPropagation();
    }
    this.removeCurrentlyHighlighted();
    this.currentlyHighlighted = this.investmentsArray[index];
    this.currentInitiativeIndex = index;
    this.investmentsArray[index].isSelected = !this.investmentsArray[index].isSelected;
    this.header = this.investmentsArray[index].name;
    this.description = this.investmentsArray[index].narrative;
  }


  removeCurrentlyHighlighted() {
    // if there is any element that is highlighted - remove its highlight
    if (this.currentlyHighlighted) {
      this.currentlyHighlighted.isSelected = false;
    }
  }

  public showChildModal(): void {
    this.currentYear = this.calcService.getValue('tlInputTeamYear');
    this.totalChecked = this.updateAllInvestmentSelections();
    this.setInitiativesEnabled();
    let count = 0;
    _.each(this.investmentsArray, (n: any) => {
      if (n.isChecked === 1) {
        count++;
      }
    });
    if (count >= this.maxChecks) {
      _.each(this.investmentsArray, (n: any) => {
        if (n.isChecked !== 1) {
          n.isEnabled = false;
        }
      });
    }
    this.cdRef.markForCheck();
    this.communicator.trigger(AppConstants.PIN_CLICKED, true);
    this.childModal.show();
  }

  updateSubmittedInitiativesForYear(year?: number) {
    let totalChecked = 0;
    const refPrefix: string = (this.initiativeType === InitiativeType.INITIATIVE) ? AppConstants.PREFIX.INITIATIVE_CHOICE :
      AppConstants.PREFIX.LEADER_ACTION_CHOICE;
    for (let i = 1; i <= this.maxChecks; i++) {
      const currentInit = 'tlInput' + refPrefix + i + AppConstants.SUFFIX.ROUND + year,
        currentInitValue = this.calcService.getValue(currentInit);
      if (currentInitValue !== '0') {
        const initNumber = parseInt(currentInitValue.replace(refPrefix, ''), 10) - 1;
        this.investmentsArray[initNumber].isChecked = year;
        this.investmentsArray[initNumber].isLocked = 1;
        if (!this.currentSelectedInvestments.some((item) => {
          return item.ID === this.investmentsArray[initNumber].ID;
        })) {
          this.currentSelectedInvestments.push(this.investmentsArray[initNumber]);
        }
        totalChecked++;
      }
    }
    return totalChecked;
  }

  // returns totalChecked investments for currentyear.
  updateAllInvestmentSelections(): number {
    let year: number = Number(this.currentYear);
    const totalChecked: any = {};
    for (year; year > 0; year--) {
      totalChecked[year] = this.updateSubmittedInitiativesForYear(year);
    }
    return totalChecked[this.currentYear];
  }

  public hideChildModal(): void {
    this.routeAnimation = (this.routeAnimation === 'flipSimple') ? 'flipReverse' : 'flipSimple';
    this.childModal.hide();
  }

  // setActiveIndex(index) {
  //   if (this.activeIndex !== index) {
  //     this.header = '';
  //     this.description = this.textService.getText('RDInvestmentInstructions');
  //   }
  //   this.activeIndex = index;
  // }

  showAlert() {
    // this.stepChildModal.show();
    this.showConfirmationModal = true;
  }

  hideAlert(confirmed: boolean = false) {
    this.showConfirmationModal = false;
    if (confirmed) {
      this.stepChildModal.hide();
      this.onSubmit();
    }
  }
}



