import { Component, OnInit, OnDestroy, Input, ChangeDetectorRef, ChangeDetectionStrategy, ViewChild } from '@angular/core';
import { CalcService, StorageService, CommunicatorService, Constants } from '@btsdigital/ngx-isomer-core';
import { AppConstants } from '../../shared/app-constants';
import { Subscription } from 'rxjs/Subscription';
import { PageType } from '../../shared/page-type.enum';

@Component({
  selector: 'isma-client',
  templateUrl: './client.component.html',
  styleUrls: ['./client.component.styl']
})
export class ClientComponent implements OnInit, OnDestroy {

  @Input() content: any[];
  @Input() clientID: number;
  pins: any[] = [];
  sequence: any[] = [];

  private currentYear = '0';
  private subscription: Subscription;

  constructor(private calcService: CalcService, private storageService: StorageService,
    private cdRef: ChangeDetectorRef, private communicator: CommunicatorService) { }

  ngOnInit() {
    this.currentYear = this.calcService.getValue('tlInputTeamYear');
    this.filterPins();
    this.storageService.getValue(AppConstants.SEQUENCE).then((sequence: string) => {
      this.sequence = sequence ? JSON.parse(sequence) : [];
      this.subscription = this.communicator
        .getEmitter(Constants.MODEL_CALC_COMPLETE)
        .auditTime(AppConstants.PIN_UPDATE_DELAY_THRESHOLD).subscribe(() => {
          this.filterPins();
          // this.cdRef.markForCheck();
        });
    });
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  private filterPins() {
    const year = this.calcService.getValue('tlInputTeamYear');
    if (this.currentYear !== year) {
      this.currentYear = year;
      this.resetPins();
      return;
    }

    this.content.forEach((pin, pinIndex) => {
      if (pin.ID === 'GEN') {
        return;
      }

      const pinRound = pin.ID.slice(pin.ID.length-1);
      if (this.clientID === Number(pin.Client) && (this.currentYear == pinRound)) {
        pin.pinIndex = pinIndex;
        pin.pinViewed = Number(this.calcService.getValue(pin.pinViewedVariable));
        pin.pinTriggered = Number(this.calcService.getValue(pin.pinTrigger));
        const isPinTriggered: number = pin.pinTriggered;
        // check if pinTriggered is not 0
        if (isPinTriggered !== 0) {
          this.updateSequence(pin);
        }
      }
    });
    this.populatePins();
  }

  private populatePins() {
    // const keys = this.sequence.reverse();
    this.pins = this.sequence.slice().reverse();
    // keys.forEach((key) => {
    //   this.pins.push(this.sequence[key]);
    // });
  }

  updateSequence(pin: any) {
    /*const length = Object.keys(this.sequence).length,
      strSeq = JSON.stringify(this.sequence),
      newIndex = length;
    if (strSeq.indexOf('\'' + pin.ID + '\'') !== -1) {
      return;
    }*/
    const seqencePresent = !(this.sequence.map((item, ind) => {
      return item.ID;
    }).indexOf(pin.ID) === -1);
    if (seqencePresent) {
      return;
    }
    this.sequence.push(pin);
    this.storageService.setValue(AppConstants.SEQUENCE, JSON.stringify(this.sequence));
  }

  resetPins() {
    this.sequence = [];
    this.storageService.setValue(AppConstants.SEQUENCE, JSON.stringify(this.sequence));
    this.pins = [];
  }
}



