import { Component, Input, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { CalcDropdownComponent as calcDDL, CalcService, CommunicatorService, IsomerXapiService, NumberFormattingPipe, TextService } from '@btsdigital/ngx-isomer-core';
import { AppConstants } from '../../shared/app-constants';

@Component({
    selector: 'isma-calcdropdown',
    templateUrl: './calcdropdown.component.html',
    styleUrls: ['./calcdropdown.component.styl']
})
export class CalcdropdownComponent extends calcDDL implements OnInit, OnDestroy {
    private status: { isopen: boolean } = { isopen: false };
    @Input() category: string;

    @ViewChild('dd') public dropdownToggle;
    list: boolean = false;

    private observerList: any = null;
    private _itemsList: any;
    private _selectedItemList = [];
    private currentYear: any;
    constructor(private calcServicePipe: CalcService, private numberFormattingPipe: NumberFormattingPipe,
        private textEngine: TextService, private communicatorService: CommunicatorService, private isomerXapiServiceLocal: IsomerXapiService) {
        super(calcServicePipe, textEngine, numberFormattingPipe, communicatorService, isomerXapiServiceLocal);
    }


    ngOnInit() {
        this.currentYear = this.calcServicePipe.getValue(AppConstants.REFS.CURRENT_ROUND);
        this.processItems();
    }

    selectCheckbox(data: any, item: string) {
        const elval = !data[item].isChecked === true ? 1 : 0;
        this.updateCheckStatus(data, item, elval);
        this.updateModel(item, data, elval);
    }

    updateModel(item: string, data: any, elval: number) {
        const value = Object.keys(data).indexOf(item) + 1;
        const category = Number(this.category.substr(-1)) + 1;
        const categoryType = this.returnCategoryType(category);
        const modelVal = 'tlInputSE' + value + categoryType + '_R1';

        // updating model
        if (data[item].isChecked) {
            this.calcServicePipe.setValue(modelVal, elval, false, this.currentYear, 'isma-calcdropdown');
            if (this._selectedItemList.indexOf(item) === -1) {
                this._selectedItemList.push(item);
            }
        } else if (!data[item].isChecked && !data[item].isLocked) {
            this.calcServicePipe.setValue(modelVal, elval, false, this.currentYear, 'isma-calcdropdown');
            if (this._selectedItemList.indexOf(item) > -1) {
                this._selectedItemList.splice(+this._selectedItemList.indexOf(item), 1);
            }
        }
    }

    returnCategoryType(categoryType: number): string {
        if (categoryType === 1) {
            return 'A';
        } else if (categoryType === 2) {
            return 'B';
        } else if (categoryType === 3) {
            return 'C';
        } else {
            return '';
        }
    }

    toggleDropdown($event: MouseEvent): void {
        this.list = !this.list;
        $event.preventDefault();
        $event.stopPropagation();
        this.status.isopen = !this.status.isopen;
    }

    processItems() {
        this._itemsList = Object.keys(this.items);
        const category = Number(this.category.substr(-1)) + 1;
        const categoryType = this.returnCategoryType(category);

        this._itemsList.forEach((el, index) => {
            const value = index + 1;
            const modelVal = 'tlInputSE' + value + categoryType + '_R1';
            const elVal = +this.calcServicePipe.getValue(modelVal);
            this.isomerXapiServiceLocal.trackDataOnLoad(this.calcServicePipe.getWorksheetName(modelVal), modelVal, 0, this.currentYear, 'isma-calcdropdown', false);

            if (elVal === 1) {
                if (+this._selectedItemList.indexOf(el) === -1) {
                    this._selectedItemList.push(el);
                }

                this.updateCheckStatus(this.items, el, elVal);
            }
        });
    }

    updateCheckStatus(data: any, item: string, elVal: number) {
        let totalCheckCount = this.getCheckedCount(data);
        if (totalCheckCount >= 2) {
            if (data[item]['isChecked']) {
                data[item]['isChecked'] = elVal;
            }
        } else {
            data[item]['isChecked'] = elVal;
        }
        totalCheckCount = this.getCheckedCount(data);
        this.checkToLock(data, totalCheckCount);
    }

    getCheckedCount(data: any) {
        const itemList = Object.keys(data);
        let totalCheck = 0;

        itemList.forEach(el => {
            data[el]['isLocked'] = false;
            if (data[el].isChecked) {
                totalCheck++;
            }
        });

        return totalCheck;
    }

    checkToLock(data: any, totalCheckCount: number) {
        const itemList = Object.keys(data);

        if (totalCheckCount >= 2) {
            itemList.forEach(el => {
                data[el]['isLocked'] = true;
            });
        }
    }

    ngOnDestroy() { }
}



