import { Component, Input, OnInit } from '@angular/core';
import { CalcStepperComponent as calcStpr } from '@btsdigital/ngx-isomer-core';

@Component({
    selector: 'isma-calcstepper',
    templateUrl: './calcstepper.component.html',
    styleUrls: ['./calcstepper.component.styl']
})
export class CalcStepperComponent extends calcStpr implements OnInit {
    @Input() disabled: boolean = false;
    value: number = 1;
    min: any = 0;
    max: any = 100;
    step: number = 1;
}



