import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import { HttpWrapperService } from '@btsdigital/ngx-isomer-core';
import { environment } from '../../../environments/environment';
import 'rxjs/add/operator/toPromise';

@Component({
  selector: 'isma-logout',
  template: `<ng-content></ng-content>`
})
export class LogoutComponent implements OnInit {

  constructor(private http: Http, private httpWrapper: HttpWrapperService) { }

  postUrl(relativeUrl: string): Promise<any> {
    const hostname = environment.hostname,
      url = window.location.protocol + '//' + hostname + relativeUrl;
    return this.http.post(url, null, { withCredentials: true }).toPromise();
  }

  ngOnInit() {
    const cfLogoutUrl = '/Wizer/CloudFront/Logout';
    const logoutUrl = '/Wizer/Wizer/Logout';
    this.postUrl(cfLogoutUrl)
      .then(() => this.postUrl(logoutUrl))
      .then(result => {
        const cookies = document.cookie.split(';');
        for (let i = 0; i < cookies.length; i++) {
          const cookie = cookies[i].split('=')[0].trim();
          if (cookie.length > 0) {
            document.cookie = cookie + '=; expires=Thu, 01 Jan 1970 00:00:00 UTC';
          }
        }
        // console.log('cookies after:'+ document.cookie)
        setTimeout(() => {
          window.location.href = window.location.protocol + '//' + window.location.hostname + ((window.location.port) ? ':' +
            window.location.port : '') + '/';
        }, 500);
      });
  }
}

