import { Component, OnInit, Input, OnDestroy, ChangeDetectorRef, Output } from '@angular/core';
import { CalcService, CommunicatorService, Constants, IsomerXapiService } from '@btsdigital/ngx-isomer-core';
import { TextHelperService } from '../../shared/text-helper.service';
import { Subscription } from 'rxjs/Subscription';
import { AppConstants } from '../../shared/app-constants';
import * as _ from 'lodash';
import { Subject } from 'rxjs/Subject';

@Component({
  selector: 'isma-branching',
  templateUrl: './branching.component.html',
  styleUrls: ['./branching.component.styl']
})
export class BranchingComponent implements OnInit, OnDestroy {

  @Input() scene: any;
  @Input() sceneId: any;
  @Output() closeModal: Subject<any> = new Subject<any>();
  private subscription: Subscription;
  public imagePath: string;
  public show: string = 'narrative';
  public questionsArr: Array<any> = [];
  public videoData: any;
  public showTranscript: boolean;
  public answer: any;
  public actualAnswer: any;
  private currentYear: any;
  private branchingDecisions: Array<any> = [];

  constructor(private calcService: CalcService, private textHelperService: TextHelperService, private communicator: CommunicatorService, private cdr: ChangeDetectorRef, private isomerXapiService: IsomerXapiService) { }

  ngOnInit() {
    this.currentYear = this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND);
    this.isomerXapiService.trackDataOnLoad(this.calcService.getWorksheetName(this.scene.pinTrigger), this.scene.pinTrigger, 1, this.currentYear, 'isma-branching');

    const content = this.textHelperService.getRawScenes().content;
    this.branchingDecisions = _.filter(content, { Client: '-1' });

    if (this.scene) {
      if (this.scene.pinImage) {
        this.imagePath = 'assets/images/' + this.scene.pinImage;
      } else {
        this.imagePath = 'assets/images/pindefault.png';
      }
    }

    this.subscription = this.communicator.getEmitter(Constants.MODEL_CALC_COMPLETE)
      .auditTime(AppConstants.PIN_UPDATE_DELAY_THRESHOLD).subscribe(() => {
        this.filterPins();
      });
    this.filterPins();
  }

  public answerSeleted(answer: any) {
    this.answer = answer;
    this.actualAnswer = String.fromCharCode(64 + Number(answer));
  }

  public switchView(isSubmit: boolean = false) {
    // console.log(this.scene.pinDecisionVariable, this.scene.pinViewedVariable);
    if (isSubmit) {
      this.calcService.setValue(this.scene.pinDecisionVariable, this.actualAnswer, false, this.currentYear, 'isma-branching');
    } else {
      this.calcService.setValue(this.scene.pinDecisionVariable, '1', false, this.currentYear, 'isma-branching');
    }
    this.calcService.setValue(this.scene.pinViewedVariable, '1', false, this.currentYear, 'isma-branching');
    this.animate();
  }

  private filterPins() {
    _.each(this.branchingDecisions, (n: any) => {
      if (n.pinTrigger && n.pinViewedVariable && n.pinDecisionVariable) {
        const isTriggered = Number(this.calcService.getValue(n.pinTrigger)) === 0 ? false : true;
        const isViewed = Number(this.calcService.getValue(n.pinViewedVariable)) === 0 ? false : true;
        const isAnswered = Number(this.calcService.getValue(n.pinDecisionVariable)) === 0 ? false : true;
        // console.log(n.pinTrigger + ': ' + isTriggered + ';', n.pinViewedVariable + ': ' + isViewed + ';', n.pinDecisionVariable + ': ' + isAnswered);
        if (isTriggered && !isViewed && !isAnswered) {
          this.scene = _.cloneDeep(n);
          if (n.PageType !== 'Read Update') {
            this.generateQuestions();
          }

          this.show = n.DisplayType;
          if (!this.show && n.PageType.toLowerCase() === 'event') {
            this.show = 'event';
          }
          if (n.DisplayType === 'video') {
            this.videoData = {
              media: n.media,
              transcript: n.narrative,
              showContinue: true
            };
          }
          this.cdr.markForCheck();
        } else if (n.DisplayType === 'feedback' && isTriggered && isViewed && isAnswered) {
          this.scene = _.cloneDeep(n);
          this.show = n.DisplayType;
        }
      }
    });
  }

  public showVideo(video: any) {
    this.videoData = video;
    this.show = 'video';
    this.showTranscript = null;
    this.animate();
    this.cdr.markForCheck();
  }

  public showTranscripts() {
    this.showTranscript = !this.showTranscript;
  }

  public backToEvent() {
    this.show = 'event';
    this.animate(true);
  }

  public continue() {
    this.switchView();
    this.closeModal.next();
  }

  private generateQuestions() {
    let i = 1;
    _.each(this.scene, (value: any, key: any) => {
      if (key.indexOf('question' + i) > -1) {
        const obj: any = {};
        obj['narrative'] = this.scene['question' + i + '_narrative'];
        obj['media'] = this.scene['question' + i + '_media'];
        obj['transcript'] = this.scene['question' + i + '_transcript'];
        this.questionsArr.push(obj);
        i++;
      }
    });
  }

  private animate(isReverse: boolean = false) {
    this.communicator.trigger(AppConstants.ANIMATE_MODAL, isReverse);
  }

  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }

}



