import { NgModule } from '@angular/core';
import { SharedModule } from '../shared/shared.module';
import { ViewsRoutingModule } from './views-routing.module';
import { DashboardComponent } from './dashboard/dashboard.component';
import { DashboardRouteComponent } from './dashboard-route/dashboard-route.component';
// import { TimerComponent } from './timer/timer.component';
import { ClientComponent } from './client/client.component';
import { PinsComponent } from './pins/pins.component';
import { InvestmentsComponent } from './investments/investments.component';
import { PinsContentComponent } from './pins-content/pins-content.component';
import { PinsFeedbackComponent } from './pins-feedback/pins-feedback.component';
import { PinsDecisionComponent } from './pins-decision/pins-decision.component';
import { DashMetricsComponent } from './dash-metrics/dash-metrics.component';
import { TextMetricComponent } from './text-metric/text-metric.component';
import { DataMetricComponent } from './data-metric/data-metric.component';
import { EndOfRoundFeedbackComponent } from './end-of-round-feedback/end-of-round-feedback.component';
import { CanDeactivateEndOfRoundFeedbackGuard } from './end-of-round-feedback/can-deactivate-end-of-round-feedback.guard';
import { IntroModule } from '../intro/intro.module';
import { ComponentsModule } from '../components/components.module';
import { CoreTeamComponent } from './start-inputs/core-team/core-team.component';
import { StartInputsComponent } from './start-inputs/start-inputs.component';
import { ChartsModule } from './charts/charts.module';
import { CalcSliderComponent } from './calcslider/calcslider';
// import { CalcOutputComponent } from '../shared/calc-output/calc-output.component';
import { StorageService, TextService, CalcService } from '@btsdigital/ngx-isomer-core';
import { FormsModule } from '@angular/forms';

@NgModule({
  imports: [
    SharedModule,
    ViewsRoutingModule,
    IntroModule,
    ComponentsModule,
    // ParticipantAppAuthoringModule
    ChartsModule,
    FormsModule
  ],
  declarations: [
    DashboardComponent,
    DashboardRouteComponent,
    ClientComponent,
    PinsComponent,
    InvestmentsComponent,
    PinsContentComponent,
    PinsFeedbackComponent,
    PinsDecisionComponent,
    DashMetricsComponent,
    TextMetricComponent,
    DataMetricComponent,
    EndOfRoundFeedbackComponent,
    CoreTeamComponent,
    StartInputsComponent,
    CalcSliderComponent,
    // CalcOutputComponent
  ],
  exports: [
    DashboardComponent,
    DashboardRouteComponent,
    ClientComponent,
    PinsComponent,
    InvestmentsComponent,
    PinsContentComponent,
    PinsFeedbackComponent,
    PinsDecisionComponent,
    DashMetricsComponent,
    TextMetricComponent,
    DataMetricComponent,
    CalcSliderComponent
  ],
  providers: [CanDeactivateEndOfRoundFeedbackGuard, StorageService, TextService, CalcService]
})
export class ViewsModule { }



