import { Component, OnInit, Input } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { BootstrapService } from '../../shared/bootstrap.service';
import { AppConstants } from '../../shared/app-constants';
import { StorageService, TextService } from '@btsdigital/ngx-isomer-core';


const MAX_TABS = 3;

@Component({
  selector: 'isma-intro',
  templateUrl: './intro.component.html',
  styleUrls: ['./intro.component.styl']
})
export class IntroComponent implements OnInit {
  titleKeys: string[];
  tab: string;
  activeIndex = 0;

  constructor(private bootstrap: BootstrapService, private router: Router,
    private storageService: StorageService, private textService: TextService,
    private activatedRoute: ActivatedRoute) { }

  ngOnInit() {
    this.titleKeys = [];
    for (let i = 0; i < MAX_TABS; i++) {
      const title = this.textService.getText('IntroTitle' + (i + 1)) || 'IntroTitle' + (i + 1);
      this.titleKeys.push(title);
    }

    this.activatedRoute.params.subscribe((params) => {
      this.tab = params['tab'];
      switch (this.tab) {
        case 'scenario':
          this.activeIndex = 0;
          break;
        case 'role':
          this.activeIndex = 1;
          break;
        case 'instructions':
          this.activeIndex = 2;
          break;
      }
    });
  }

  startSim() {
    this.storageService
      .setValue(AppConstants.INTRO_VISITED, 'true');
      // dont need to wait for data to get saved on pulse backend
      this.bootstrap.appReady = true;
      this.router.navigateByUrl(AppConstants.ROUTES.DASHBOARD);
  }
}



