import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { DashboardComponent } from './dashboard/dashboard.component';
import { CalcModelLoadedGuard } from '@btsdigital/ngx-isomer-core';
import { DashboardRouteComponent } from './dashboard-route/dashboard-route.component';
import { EndOfRoundFeedbackComponent } from './end-of-round-feedback/end-of-round-feedback.component';
import { CanDeactivateEndOfRoundFeedbackGuard } from './end-of-round-feedback/can-deactivate-end-of-round-feedback.guard';
import { IntroComponent } from '../intro/intro/intro.component';
import { StartHoldComponent } from '../intro/start-hold/start-hold.component';
import { IntroStartedGuard } from '../intro/intro-started.guard';
import { IntroVisitedGuard } from '../intro/intro-visited.guard';
import { StartInputsComponent } from './start-inputs/start-inputs.component';

const routes: Routes = [
  {
    path: 'dashboard',
    component: DashboardRouteComponent,
    canActivate: [CalcModelLoadedGuard]
  },
  {
    path: 'roundfeedback',
    canActivate: [CalcModelLoadedGuard],
    component: DashboardRouteComponent,
    children: [{
      component: EndOfRoundFeedbackComponent,
      canDeactivate: [CanDeactivateEndOfRoundFeedbackGuard],
      path: ':year',
      outlet: 'year'
    }]
  },
  {
    path: 'intro',
    canActivate: [CalcModelLoadedGuard, IntroStartedGuard, IntroVisitedGuard],
    component: IntroComponent
  },
  {
    path: 'intro/:tab',
    canActivate: [CalcModelLoadedGuard],
    component: IntroComponent,
    children: [{
      path: 'scenario',
      component: IntroComponent,
    }, {
      path: 'role',
      component: IntroComponent,
    }, {
      path: 'instructions',
      component: IntroComponent,
    } ]
  },
  {
    path: 'start',
    canActivate: [CalcModelLoadedGuard, IntroVisitedGuard],
    component: StartInputsComponent
  }];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ViewsRoutingModule { }



