import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { CalcService, CommunicatorService, Constants } from '@btsdigital/ngx-isomer-core';
import { AppConstants } from '../../shared/app-constants';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'isma-data-metric',
  templateUrl: './data-metric.component.html',
  styleUrls: ['./data-metric.component.styl']
})
export class DataMetricComponent implements OnInit, OnDestroy {
  @Input() businessUnitId: number;
  private subscription: Subscription;
  metricValues: number[] = [];
  metricRefs: string[] = [];
  currentRound: string;
  metricFormats: string[] = [];
  constructor(private calcService: CalcService, private communicator: CommunicatorService) { }

  ngOnInit() {
    this.currentRound = this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND);
    this.metricFormats = ['0', '0', '0'];
    this.metricRefs = [
      AppConstants.PREFIX.BU_METRIC + this.businessUnitId + AppConstants.SUFFIX.BU_SUMMARY_METRIC + '6' +
          AppConstants.SUFFIX.ROUND  + this.currentRound,
      AppConstants.PREFIX.BU_METRIC + this.businessUnitId + AppConstants.SUFFIX.BU_SUMMARY_METRIC + '7' +
          AppConstants.SUFFIX.ROUND  + this.currentRound,
      AppConstants.PREFIX.BU_METRIC + this.businessUnitId + AppConstants.SUFFIX.BU_SUMMARY_METRIC + '8' +
          AppConstants.SUFFIX.ROUND  + this.currentRound
    ];

    this.subscription = this.communicator.getEmitter(Constants.MODEL_CALC_COMPLETE).subscribe(() => {
      this.updateMetricValues();
    });

    this.updateMetricValues();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();

  }

  updateMetricValues() {
    this.currentRound = this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND);
    this.metricRefs = [
      AppConstants.PREFIX.BU_METRIC + this.businessUnitId + AppConstants.SUFFIX.BU_SUMMARY_METRIC + '6' +
          AppConstants.SUFFIX.ROUND  + this.currentRound,
      AppConstants.PREFIX.BU_METRIC + this.businessUnitId + AppConstants.SUFFIX.BU_SUMMARY_METRIC + '7' +
          AppConstants.SUFFIX.ROUND  + this.currentRound,
      AppConstants.PREFIX.BU_METRIC + this.businessUnitId + AppConstants.SUFFIX.BU_SUMMARY_METRIC + '8' +
          AppConstants.SUFFIX.ROUND  + this.currentRound
    ];

    this.metricRefs.forEach((ref, index) => {
      this.metricValues[index] = Number(this.calcService.getValue(ref));
    });
  }
}



