import { Component, OnInit, Input, OnDestroy, ChangeDetectorRef, ChangeDetectionStrategy } from '@angular/core';
import { CalcService, CommunicatorService, Constants, NumberFormatting, IsomerXapiService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs/Subscription';
import { AppConstants } from '../app-constants';

@Component({
  selector: 'isma-eps-chart',
  templateUrl: './eps-chart.component.html',
  styleUrls: ['./eps-chart.component.styl'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class EpsChartComponent implements OnInit, OnDestroy {

  @Input() ref: string;
  @Input() max = 10;
  @Input() labelKey: string;

  private subscription: Subscription;
  value: string;
  percent: string;
  perc: number;

  constructor(private calcService: CalcService, private communicator: CommunicatorService,
    private cdRef: ChangeDetectorRef, private isomerXapiService: IsomerXapiService) { }

  ngOnInit() {

    this.isomerXapiService.trackDataOnLoad(this.calcService.getWorksheetName(this.ref), this.ref, this.labelKey, this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND), 'isma-eps-chart');

    this.subscription = this.communicator.getEmitter(Constants.MODEL_CALC_COMPLETE).subscribe(() => {
      this.onModelUpdate();
    });
    this.onModelUpdate();
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  private onModelUpdate() {
    this.value = this.calcService.getValue(this.ref, true);
    this.perc = Number(this.value) / Number(this.max);
    this.perc *= 100;
    this.percent = NumberFormatting.format(this.perc, '0.0%');
    this.cdRef.markForCheck();
  }
}



