import {
    Component, Input, OnInit, OnDestroy, EventEmitter, OnChanges,
    SimpleChanges, SimpleChange, ChangeDetectorRef
} from '@angular/core';
// import { CalcService } from './calc.service';
import { CalcService, NumberFormattingPipe, CommunicatorService, Constants, IsomerXapiService } from '@btsdigital/ngx-isomer-core';
import { Subscription } from 'rxjs/Subscription';
import { AppConstants } from '../app-constants';

@Component({
    selector: 'isma-calc-output',
    styleUrls: ['./calc-output.component.styl'],
    templateUrl: './calc-output.component.html',
    providers: [NumberFormattingPipe]
})

export class CalcOutputComponent implements OnInit, OnDestroy, OnChanges {
    @Input() ref: string;
    @Input() format: string;
    @Input() scaler = 1;
    @Input() yearRef: string;
    @Input() lastYearRef: string;
    @Input() showBalloon = false;
    @Input() showValue: string;
    @Input() isGauge = false;
    @Input() isTopGauge = false;
    @Input() placement = 'top';
    @Input() ignoreRefChangeDifference = false;
    showValueBoolean: boolean;
    showBalloonValue = false;
    showMetricBalloon = false;
    differenceInValue: string;
    changeInValue: number;
    value: string;
    newValue: string;
    subscriber: Subscription;
    pinClickSubscriber: Subscription;
    year: string;
    timeoutRef: any;
    animTimeoutRef: any;
    hasRefChanged = false;
    constructor(private calcService: CalcService, private numberFormatting: NumberFormattingPipe, private cdRef: ChangeDetectorRef,
        private communicator: CommunicatorService, private isomerXapiService: IsomerXapiService) { }

    ngOnInit() {
        this.isomerXapiService.trackDataOnLoad(this.calcService.getWorksheetName(this.ref),
            this.calcService.getRefNameByYearRef(this.ref, this.yearRef),
            this.ref, this.yearRef ? this.calcService.getValue(this.yearRef) : this.calcService.getRoundValue(), 'isma-calc-output');
        if (this.showValue === 'false') {
            this.showValueBoolean = false;
        } else {
            this.showValueBoolean = true;
        }
        this.value = this.calcService.getValueForYear(this.ref, this.yearRef, true);
        // this.value = this.calcService.getValueForYear(this.ref, this.yearRef);
        this.subscriber = this.communicator.getEmitter(Constants.MODEL_CALC_COMPLETE).subscribe(() => {
            this.onModelUpdate();
        });
        this.pinClickSubscriber = this.communicator.getEmitter(AppConstants.PIN_CLICKED).subscribe(() => {
            this.hideBalloon();
            this.cdRef.markForCheck();
        });
    }

    onModelUpdate() {
        this.newValue = this.calcService.getValueForYear(this.ref, this.yearRef, true);
        if (this.value === this.newValue) {
            return;
        }
        if (this.showBalloon) {
            this.calculateDifference();
            if (this.showBalloonValue && !this.hasRefChanged) {
                this.showMetricBalloon = true;
                this.timeoutRef = setTimeout(() => {
                    this.showMetricBalloon = false;
                    // allow for fadeout animation
                    this.animTimeoutRef = setTimeout(() => {
                        this.showBalloonValue = false;
                    }, 3000);
                    this.cdRef.markForCheck();
                }, 20000);
            } else if (this.hasRefChanged) {
                this.showBalloonValue = false;
            }
        }
        this.value = this.newValue;
    }

    hideBalloon() {
        if (this.timeoutRef) {
            clearTimeout(this.timeoutRef);
        }
        if (this.animTimeoutRef) {
            clearTimeout(this.animTimeoutRef);
        }
        this.showBalloonValue = false;
        this.showBalloonValue = false;
    }

    ngOnDestroy() {
        this.subscriber.unsubscribe();
    }

    ngOnChanges(changes: SimpleChanges) {
        const change: SimpleChange = changes['ref'];

        if (change && !change.isFirstChange()) {
            if (this.ignoreRefChangeDifference) {
                this.hasRefChanged = true;
                this.onModelUpdate();
                this.hasRefChanged = false;
            } else {
                this.onModelUpdate();
            }
        }
    }

    calculateDifference() {
        const curValue: number = Number(this.numberFormatting.parse(this.newValue)),
            prevValue: number = Number(this.numberFormatting.parse(this.value));
        let difference: string;
        if (curValue > prevValue) {
            difference = '' + (curValue - prevValue);
            this.showBalloonValue = true;
            this.changeInValue = 2;
        } else if (curValue < prevValue) {
            difference = '' + (prevValue - curValue);
            this.showBalloonValue = true;
            this.changeInValue = 1;
        } else {
            difference = '0';
            this.changeInValue = 0;
        }
        this.differenceInValue = String(this.numberFormatting.transform(difference, this.format));
    }
}



