import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { Router } from '@angular/router';
import { StorageService } from '@btsdigital/ngx-isomer-core';
import { AppConstants } from '../../shared/app-constants';
import { environment } from '../../../environments/environment';

@Component({
  selector: 'isma-start-hold',
  templateUrl: './start-hold.component.html',
  styleUrls: ['./start-hold.component.styl']
})
export class StartHoldComponent implements OnInit {
  connectToPulse = environment.connectToPulse;
  constructor(private storageService: StorageService, private router: Router, private cdRef: ChangeDetectorRef) { }

  ngOnInit() {
    this.connectToPulse = environment.connectToPulse;
    this.cdRef.markForCheck();
  }

  startIntro() {
    this.storageService
      .setValue(AppConstants.INTRO_STARTED, 'true');
      // dont need to wait for request to complete before jumping in
      this.router.navigateByUrl(AppConstants.INTRO_ROUTE);
  }

}



