import { NgModule, ModuleWithProviders } from '@angular/core';
import { ChartDefaults } from './chartdefaults';
import { HighchartsStatic } from './highchartsstatic';
import { BasicAreaComponent } from './basicarea/basicarea';
import { BubbleChartComponent } from './bubble/bubblechart';
import { BasicLineComponent } from './basicline/basicline';

const DIRECTIVES = [BasicAreaComponent, BubbleChartComponent, BasicLineComponent];

export function highChartsDepsLoader() {
    const hc = require('highcharts'),
        hm = require('highcharts/highcharts-more'),
        sg = require('highcharts/modules/solid-gauge'),
        heatMap = require('highcharts/modules/heatmap'),
        exp = require('highcharts/modules/exporting');

    hm(hc);
    sg(hc);
    heatMap(hc);
    exp(hc);

    return hc;
}


@NgModule({
    imports: [],
    declarations: [DIRECTIVES],
    exports: [DIRECTIVES],
    providers: [ChartDefaults, { provide: HighchartsStatic, useFactory: highChartsDepsLoader}]
})
export class ChartsModule {
    // static forRoot(highchartsStatic: HighchartsStatic, ...highchartsModules: Array<Function>): ModuleWithProviders {
    //     // Plug highcharts modules
    //     highchartsModules.forEach((module) => {
    //         module(highchartsStatic)
    //     });

    //     return {
    //         ngModule: ChartsModule,
    //         providers: [
    //             { provide: HighchartsStatic, useValue: highchartsStatic }
    //         ]
    //     }
    // }
}



