import { Component, OnInit, OnDestroy, ViewChild, Input, Output, EventEmitter, ComponentFactoryResolver } from '@angular/core';
import { CalcService, CommunicatorService, Constants } from '@btsdigital/ngx-isomer-core';
import { AppConstants } from '../../shared/app-constants';
import { PageType } from '../../shared/page-type.enum';
import { Subscription, Observable } from 'rxjs/Rx';
import { ContentHostDirective } from '../../shared/content-host.directive';
import { SingleSelectComponent } from '../../components/single-select/single-select.component';
import { MultiSelectComponent } from '../../components/multi-select/multi-select.component';
import { RadioSelectionComponent } from '../../components/radio-selection/radio-selection.component';
import * as _ from 'lodash';
import { HotspotComponent } from '../../components/hotspot/hotspot.component';
import { DraglistComponent } from '../../components/draglist/draglist.component';

@Component({
  selector: 'isma-pins-decision',
  templateUrl: './pins-decision.component.html',
  styleUrls: ['./pins-decision.component.styl']
})
export class PinsDecisionComponent implements OnInit, OnDestroy {

  @Input() scene: any;
  @Input() pinId: string;
  @Input() type: number;
  @Input() decisionType: string;
  @Input() answers?: any;
  // private eventType = 'singleSelect';
  isClosing = false;
  isViewed = false;
  answer = 0;
  title: string;
  private subscription: Subscription;
  selectedOptions: Array<any> = [];

  @ViewChild(ContentHostDirective) decisionHolder: ContentHostDirective;

  @Output() decisionSubmitted: EventEmitter<any> = new EventEmitter<any>();
  @Output() cancel: EventEmitter<any> = new EventEmitter<any>();

  constructor(private calcSerice: CalcService, private communicator: CommunicatorService,
    private componentFactoryResolver: ComponentFactoryResolver) { }

  ngOnInit() {
    if (this.scene) {
      this.isClosing = false;
      this.title = this.scene.name;
      this.answer = Number(this.calcSerice.getValue('tlInput' + this.scene.ID)) || 0;
      if (this.type === PageType.EVENT && this.decisionType === 'hotspot') { // add subtypes once we have more decision types
        const obs = this.loadComponent(HotspotComponent, this.scene, 'optionSelected', 'data');
        if (obs) {
          this.subscription = obs.subscribe((option) => {
            this.onAnswerSelected(option);
          });
        }
      } else if (this.type === PageType.EVENT && this.decisionType === 'DragDrop') {
        const obs = this.loadComponent(DraglistComponent, this.scene, 'valuesUpdated', 'data');
        if (obs) {
          this.subscription = obs.subscribe((option) => {
            this.onAnswerSelected(option);
          });
        }
      }else if(this.type === PageType.EVENT && this.decisionType === 'MultiSelect'){
        const obs = this.loadComponent(MultiSelectComponent, this.scene, 'selectedAnswer', 'data');
        if (obs) {
          this.subscription = obs.subscribe((option) => {
            this.onAnswerSelected(option);
          });
        }
      }else if (this.type === PageType.EVENT) { // add subtypes once we have more decision types
        const data = {
          id: this.pinId,
          scene: this.scene
        };

        const obs = this.loadComponent(SingleSelectComponent, data, 'selectedAnswer', 'data');
        if (obs) {
          this.subscription = obs.subscribe((answer) => {
            // this.onAnswerSelected(answer);
          });
        }
      } else if (this.type === PageType.MATRIX) {
        const data = {
          radioData: this.scene.matrixData,
          answers: this.answers
        };
        const obs = this.loadComponent(RadioSelectionComponent, data, 'optionSelected', 'data');
        if (obs) {
          this.subscription = obs.subscribe((option) => {
            const foundIndex = _.findIndex(this.selectedOptions, { index: option.index });
            if (foundIndex === -1) {
              this.selectedOptions.push(option);
            } else {
              this.selectedOptions[foundIndex].option = option.option;
            }

            if (this.selectedOptions.length === this.scene.matrixData.length - 1) {
              this.answer = -1;
            }
          });
        }
      }
    }
  }

  loadComponent(component: any, componentData: any, subscribeToFunction: string, dataVar: string): Observable<any> {
    const componentFactory = this.componentFactoryResolver.resolveComponentFactory(component);
    const viewContainerRef = this.decisionHolder.viewContainerRef;

    viewContainerRef.clear();

    if (this.subscription) {
      this.subscription.unsubscribe();
      this.subscription = null;
    }

    const componentRef = this.decisionHolder.viewContainerRef.createComponent(componentFactory);
    componentRef.instance[dataVar] = componentData;

    return componentRef.instance[subscribeToFunction];
  }

  onSubmit() {
    // const pinDecisionVariable = this.scene.pinDecisionVariable,
    //   selectedChoice = (this.type === PageType.EVENT) ? String.fromCharCode(64 + Number(this.answer)) :
    //     (this.type === PageType.OPPORTUNITY) ? (this.answer === 1) ? 1 : -1 : this.answer;
    this.isViewed = true;
    if (this.type === PageType.MATRIX) {
      this.decisionSubmitted.emit(this.selectedOptions);
    } else if (this.decisionType == 'MultiSelect') {
      this.decisionSubmitted.emit(this.answers);
    } else {
      this.decisionSubmitted.emit(this.answer);
    }
  }

  onAnswerSelected(answer) {
    if (this.decisionType == 'MultiSelect'){
      this.answers = answer;
      this.answer = this.answers.reduce((a, b) => { return parseInt(a) + parseInt(b) });
    }else{
      this.answer = answer;
    }
  }

  onCancel() {
    this.cancel.emit();
  }

  ngOnDestroy() {
    this.isClosing = false;
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }

  checkIfSubmitAvailable(){
    if (this.decisionType == 'MultiSelect'){
      if(this.answers){
        let sum = this.answers.reduce((a, b) => { return parseInt(a) + parseInt(b) });
        if(sum > 0){
          return true;
        }        
      }
    } else if (this.decisionType != 'MultiSelect'){
      if (!this.answers || this.answers.length === 0) {
        return true
      }
    }

    return false;
  }
}



