import { Component, ElementRef, EventEmitter, Input, Output, OnInit, OnDestroy } from '@angular/core';
// import * as numberFormatting from '../../../libs/jsCalc/numberFormatting';
import { CalcService, IsomerXapiService } from '@btsdigital/ngx-isomer-core';
// import { CalcService } from '../calc.service';
import * as noUiSlider from 'nouislider';
import { AppConstants } from '../../shared/app-constants';


@Component({
    selector: 'isma-calc-slider',
    templateUrl: './calcslider.html',
    styleUrls: ['./calcslider.css']
})

export class CalcSliderComponent implements OnInit, OnDestroy {
    @Input() ref: string = '';
    @Input() yearRef: string;
    @Input() step: number;
    @Input() min: number;
    @Input() max: number;
    @Input() showButtons: boolean = false;
    @Input() onChange: any;
    @Input() minText = '';
    @Input() maxText = '';
    @Input() middleText = '';

    @Output() currentValue: EventEmitter<string> = new EventEmitter<string>();
    private value: any;
    private tempValue: string;
    private year: string;
    private isDirty: boolean = false;
    private el: any;
    private currentYear: any;
    constructor(private calcService: CalcService, private elRef: ElementRef, private isomerXapiService: IsomerXapiService) {
        this.el = elRef.nativeElement;
    }

    ngOnInit() {
        this.currentYear = this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND);
        this.isomerXapiService.trackDataOnLoad(this.calcService.getWorksheetName(this.ref), this.ref, 0, this.currentYear, 'calc-slider', false);
        const pipFormats = { '0': '0', '1': '', '2': '', '3': '', '4': '4' };
        if (this.min && !isNaN(this.min) && (this.minText !== '')) {
            pipFormats[this.min] = this.minText;
        }
        if (this.max && !isNaN(this.max) && (this.maxText !== '')) {
            pipFormats[this.max] = this.maxText;
        }

        pipFormats[2] = this.middleText; // to set neutral text;

        /*if (this.calcService) {
            this.value = this.calcService.getValue(this.ref);
            this.value = numberFormatting.unformat(this.value);
            this.value = String(this.value);
        }*/

        const slider: HTMLElement = this.el.querySelector('.calc-slider');
        // let pips: Array<HTMLElement> = this.el.getElementsByClassName('pip-steps');
        const opt: any = {
            start: [Number(this.value) || Number(this.min)],
            range: {
                'min': Number(this.min),
                'max': Number(this.max)
            },
            step: (this.step) ? Number(this.step) : undefined,
            behavior: 'tap',
            pips: {
                mode: 'steps',
                density: 100 / this.max,
                format: {
                    to: function (a) {
                        return pipFormats[a];
                    }
                }
            }
        };

        if (slider) {
            // for (var count = 0; count < sliders.length; count++) {
            //     noUiSlider.create(sliders[count], opt);
            // }
            noUiSlider.create(slider, opt);

            this.bindEvents(slider);
        }


    }

    bindEvents(slider: any) {
        if (slider && slider.noUiSlider) {
            slider.noUiSlider.on('change', () => {
                const curVal = slider.noUiSlider.get();
                this.saveDataToModel(curVal);
                // console.log('onchange called');
                if (this.onChange) {
                    this.onChange(this.el);
                }
            });
        }
    }

    saveDataToModel(val: any) {
        // console.log('calcslider save to model called: ' + this.ref + ', ' + val);
        // this.isDirty = false;
        this.calcService.setValue(this.ref, val, false, this.currentYear, 'calc-slider');
        this.currentValue.emit(val);
    }


    ngOnDestroy() {
    }

}



