import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import {
  CalcService, CommunicatorService, Constants, HttpWrapperService, IsomerXapiService, ManifestService,
  StorageService, SyncService, SyncStatusService, TextService
} from '@btsdigital/ngx-isomer-core';
import { SignalRService } from '@btsdigital/pulsesignalr';
import { LoggerService, XApiService } from '@btsdigital/pulseutilities';
import * as jQuery from 'jquery/dist/jquery.min';
import { environment } from '../../environments/environment';
import { manifest } from '../manifest.config';
import { ModelLoaderService } from '../model/model-loader.service';
import { AppConstants } from './app-constants';

window['__theme'] = 'bs3';
window['jQuery_1_6_4'] = jQuery.noConflict(true);

@Injectable()
export class BootstrapService {

  // private modelLoadProgressSub: Subscription;
  private textServiceInitPromise: Promise<any>;
  private modelLoadCompletePromise: Promise<any>;
  private isAppReady = false;
  private appConfigInitialized = false;
  userObj: any;
  // private testNotify = new notifyComponent();
  constructor(private calcService: CalcService, private textService: TextService,
    private storageService: StorageService, private syncStatusService: SyncStatusService,
    private communicator: CommunicatorService, private logger: LoggerService,
    private modelLoader: ModelLoaderService, private signalR: SignalRService, private syncService: SyncService,
    private manifestService: ManifestService, private httpWrapperService: HttpWrapperService,
    private xpiService: XApiService, private isomerXapiService: IsomerXapiService) {

    this.userObj = JSON.parse(localStorage.getItem('user'));
    this.xpiService.configureAngularticsUrl(manifest.nodejsUrl);
  }

  initAppConfig() {
    // if (environment.connectToPulse === true) {
    // const zoneMatch = environment.hostname.match(/cisco-zone(\d).*/i);
    const SERVER_ZONE = (environment.production) ? window.localStorage.getItem('ManifestHost') : undefined;
    // environment.hostname = (zoneMatch.length) ? 'cisco-zone' + SERVER_ZONE_NUMBER + '.btspulse.com' : environment.hostname;
    if(SERVER_ZONE){
      environment.hostname = SERVER_ZONE;//environment.hostnames[Number(SERVER_ZONE_NUMBER) - 1];
    }
    const HOSTNAME: string = (environment.CDN0LOCAL1LOCALHOST2 === 0) ?
      'https://' + environment.hostname + '/' : (environment.CDN0LOCAL1LOCALHOST2 === 1) ?
        window.location.protocol + '//' + window.location.hostname : 'http://localhost';
    manifest.hostName = HOSTNAME;
    manifest.serviceUrl = HOSTNAME + '/PulseServices/';
    this.manifestService.setConfig({ config: manifest });
    this.httpWrapperService.setHostName(manifest.hostName);
    this.signalR.configureSignalRService({ hostname: manifest.hostName, serviceUrl: manifest.serviceUrl, hasHeartBeat: false });
    this.isomerXapiService.configureIsomerAppService({
      enabletracking: manifest.enableTracking, statusCompletionQuestions: manifest.statusCompletionQuestions,
      rangeRefTags: manifest.rangeRefTags, enableLoadLevelTracking: manifest.enableLoadLevelTracking
    });

    // set the user for tracking
    if (manifest.enableTracking) {
      try {
        if (this.userObj) {
          this.xpiService.setUserProperties(this.userObj.pid, this.userObj.pname, this.userObj.pemail, this.userObj.planguagekey,
            this.userObj.token, this.userObj.moduleName, this.userObj.userExperience, manifest.hostName, manifest.activityType,
            this.userObj.userExperience, manifest.platform, manifest.defaultEmail, manifest.lrsUrl, manifest.serviceUrl, manifest.enableEmailEncryption);
        }
      } catch (e) {
        this.logger.log('Error initializing UserProperties: ' + e);
      }
    }
    // }
    this.logger.enableLogging(false);
    // set mode for storageService
    this.storageService.setThrottleDuration(30000); // set throttle to 30s between each push.
    this.storageService.setMode(environment.connectToPulse ? Constants.STORAGE_MODES.MIXED : Constants.STORAGE_MODES.LOCAL);
    this.appConfigInitialized = true;
  }

  init(): Promise<any> {
    if (!this.appConfigInitialized) {
      this.initAppConfig();
    }
    return this.modelLoader.getModel().then((response) => {
      const model = response;
      // set initial language to be loaded
      this.textService.language = 'en';
      // load text content
      this.textServiceInitPromise = this.textService.init();
      // try and load the calc model
      this.modelLoadCompletePromise = this.calcService
        .getApi(model)
        .catch((err) => {
          this.logger.log('Error when loading model ', err);
        });
      return Promise.all([this.textServiceInitPromise, this.modelLoadCompletePromise])
        .then(() => {
          if (environment.connectToPulse === true) {
            this.syncStatusService.SetModelStateStatus(0);
            this.syncService.initializeSync().then(() => {
              this.syncService.setMode(Constants.CONNECTION_MODE.PUSH);
            });
          }
        });
    });
  }

  public set appReady(v: boolean) {
    this.isAppReady = v;
    this.communicator.trigger(AppConstants.APP_READY, v);
  }

  public get appReady(): boolean {
    return this.isAppReady;
  }
}



