import { Component, HostListener, OnInit, ViewChild, OnDestroy } from '@angular/core';
import { CommunicatorService, StorageService, SyncStatusService } from '@btsdigital/ngx-isomer-core';
import { ModalDirective, } from 'ngx-bootstrap';
import { AppConstants } from './shared/app-constants';
import { BootstrapService } from './shared/bootstrap.service';
import { Observable } from 'rxjs/Observable';
import { Subscription } from 'rxjs/Subscription';

const KEY_NUM_3 = 51;

@Component({
  selector: 'isma-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.styl']
})
export class AppComponent implements OnInit, OnDestroy {
  isAppReady = false;
  private status: number;
  private isCalcDebuggerActive: boolean = false;

  @ViewChild('networkErrorModal') public networkErrorModalRef: ModalDirective;
  @ViewChild('debugModal') modal: ModalDirective;
  animateOpen: boolean = false;
  calcService: any;
  networkSubscriber: Subscription;

  constructor(private communicator: CommunicatorService, private storageService: StorageService,
    private bootstrap: BootstrapService, private syncStatusService: SyncStatusService) { }

  ngOnInit() {
    // show dashboard only after APP_READY is set from bootstrap
    this.bootstrap.initAppConfig();
    this.communicator.getEmitter(AppConstants.APP_READY).subscribe((value) => {
      this.isAppReady = ((value + '') === 'true') ? true : false;
      if(this.isAppReady){
        this.attachNetworkPopup();
      }
    });
  }

  @HostListener('document:keyup', ['$event'])
  openModal($event: KeyboardEvent) {
    if ($event.ctrlKey && $event.shiftKey && $event.keyCode === KEY_NUM_3) {
      this.animateOpen = true;
      this.isCalcDebuggerActive = true;
      this.modal.show();
    }
  }

  attachNetworkPopup(){
    this.networkSubscriber = this.syncStatusService.Status.subscribe((status: any) => {
      this.status = status;
      this.checkNetWorkPopup();
    });

    let online$ = Observable.fromEvent(window, 'online'),
        offline$ = Observable.fromEvent(window, 'offline');
    
    online$.subscribe(e => {
      this.calcService.setValue(AppConstants.REFS.CURRENT_ROUND,this.calcService.getValue(AppConstants.REFS.CURRENT_ROUND));
    });
    
    offline$.subscribe(e => {
      this.showNetworkAlert();
    });
  }

  checkNetWorkPopup() {
    if(this.status==2||this.status==3) {
      this.showNetworkAlert();
    }
  }

  showNetworkAlert() {
    this.networkErrorModalRef.show();
  }
  
  hideNetworkAlert() {
    this.networkErrorModalRef.hide();
  }

  ngOnDestroy(){
    if(this.networkSubscriber){
      this.networkSubscriber.unsubscribe();
    }
  }
}



