import { Component, Input, OnInit } from '@angular/core';

@Component({
    selector: 'isma-core-team',
    templateUrl: './core-team.component.html',
    styleUrls: ['./core-team.component.css']
})
export class CoreTeamComponent implements OnInit {
    @Input() teamData;
    @Input() lstHeight;

    constructor() { }

    ngOnInit() {
        if (!this.lstHeight) {
            this.lstHeight = '90px';
        }
    }
}



