import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Subject } from 'rxjs/Subject';
import { CalcService, TextService } from '@btsdigital/ngx-isomer-core';
import { TextHelperService } from '../../shared/text-helper.service';
import * as _ from 'lodash';

@Component({
    selector: 'isma-radio-selection',
    templateUrl: './radio-selection.component.html',
    styleUrls: ['./radio-selection.component.styl']
})
export class RadioSelectionComponent implements OnInit {

    @Input() data: any;
    radioData: any;
    answers: any = [];

    id: string;
    scene: any;
    isAnswered: boolean = false;
    isSubmitEnabled: boolean = false;
    correctAnswers = [];

    @Output() continue: EventEmitter<any> = new EventEmitter<any>();
    @Output() optionSelected: Subject<any> = new Subject<any>();

    constructor(private calcService: CalcService, private textHelper: TextHelperService, private textEngineService: TextService) { }

    ngOnInit() {
        if (this.data) {
            this.id = this.data.ID;
            this.scene = this.data.scene;

            const curVal = this.calcService.getValue('tlInput' + this.id);

            let readValue = this.calcService.getValue('tlInput_READ_' + this.id);
            this.isAnswered = readValue == "0" ? false : true;

            if (this.isAnswered) {
                this.onContinue(true);
            }



            this.radioData = this.textHelper.processMatixData(this.textEngineService.getScene(this.id))//this.data.radioData;
            for (let i = 0; i < this.radioData.length; i++) {
                if(this.radioData[i].isHeader){
                    continue;
                }
                if (this.isAnswered) {
                    let val = this.calcService.getValue('tlInput' + this.data.rangePrefix + i + this.data.rangeSuffix);
                    this.answers.push({ index: i, option: val.charCodeAt(0) - 64 });
                } else {
                    this.answers.push({ index: i, option: -1 });
                }

                this.correctAnswers.push({ index: i, option: this.radioData[i].correctKey });
            }
            // this.answers = this.data.answers;
        }
    }

    onSelect(index: number, option: any) {
        // this.optionSelected.next({ index: index, option: option });

        const foundIndex = _.findIndex(this.answers, { index: index });
        if (foundIndex === -1) {
            this.answers.push({ index: index, option: option });
        } else {
            this.answers[foundIndex].option = option;
        }

        if (_.findIndex(this.answers, { option: -1 }) === -1) {
            this.isSubmitEnabled = true;
        }
    }

    onSubmit() {
        this.isAnswered = true;
        //Set Values
        for (let i = 0; i < this.answers.length; i++) {
            this.calcService.setValue('tlInput' + this.data.rangePrefix + this.answers[i].index + this.data.rangeSuffix, String.fromCharCode(64 + this.answers[i].option));
        }
        //Set Pin Complete
        this.calcService.setValue('tlInput' + this.id, 1);
        this.calcService.setValue('tlInput_READ_' + this.id, 1);
    }

    onContinue(alreadySubmitted: boolean) {
        this.continue.emit(alreadySubmitted)
    }

}



