import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { StorageService } from '@btsdigital/ngx-isomer-core';
import { AppConstants } from '../shared/app-constants';

@Injectable()
export class IntroStartedGuard implements CanActivate {
  constructor (private storageService: StorageService, private router: Router) {}
  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
    return this.storageService.getValue(AppConstants.INTRO_STARTED).then((started: string) => {
      if (started === 'true') {
        return true;
      } else {
        this.router.navigateByUrl(AppConstants.SIM_START_ROUTE);
        return false;
      }
    });
  }
}



